.text-field {
    &__name {
        &-expandable{
            padding: 10px 0 0 0;
            font-weight: 700;
            font-size: 12px;
            line-height: calc(15 / 12 * 100%);
            text-transform: uppercase;
            color: #1F2041;
            cursor: pointer;
        }
    }
    &__expandable-checkbox-list-hidden.\.active-expandble{
        display: block;
        position: absolute;
        background: #ffffff;
        z-index: 3;
    }
    &__expandable-checkbox-list-hidden {
        display: none;
        width: 266px;
        height: inherit;
        & li {
            line-height: 18px;
            color: rgba(31, 32, 65, 0.5);
            margin: 0 0 10px 0px;
            &:first-child {
                margin-top: 5px;
            }
            &:last-child {
                margin-bottom: 0px;
            }
        }
        & span {
            font-weight: 700;
            line-height: 18px;
            color: rgba(31, 32, 65, 0.75);
        }
        & p {
            font-size: 12px;
            line-height: 14px;
            color: rgba(31, 32, 65, 0.5);
        }
        & input[type="checkbox"]:checked,
         input[type="checkbox"]:not(:checked){
            position: absolute;
            opacity: 0;
            visibility: hidden;
            z-index: -1;
        }
        & input[type="checkbox"]:checked + label,
         input[type="checkbox"]:not(:checked) + label{
            position: relative;
            display: inline-block;
            padding-left: 30px;
            line-height: 21px;
            cursor: pointer;
        }
        & input[type="checkbox"]:not(:checked) + label::before{
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 20px;
            height: 20px;
            border: 1px solid rgba(31, 32, 65, 0.25);
            border-radius: 4px;
        }
        & input[type="checkbox"]:checked + label::before{    
                content: "";
                position: absolute;
                top: 0;
                left: 0;
                width: 20px;
                height: 20px;
                border: 1px solid #8BA4F9;
                border-radius: 4px;
        }
       & input[type="checkbox"]:checked + label::after,
       input[type="checkbox"]:not(:checked) + label::after{
            content: "";
            position: absolute;
            top: 6px;
            left: 6px;
            width: 8px;
            height: 6px;
            border-left: 2px solid #8BA4F9;
            border-bottom: 2px solid #8BA4F9;
            -webkit-transform: rotate(-45deg);
            -moz-transform: rotate(-45deg);
            -o-transform: rotate(-45deg);
            -ms-transform: rotate(-45deg);
            transform: rotate(-45deg);
            -webkit-transition: all 0.2s ease;
            -moz-transition: all 0.2s ease;
            -o-transition: all 0.2s ease;
            transition: all 0.2s ease;
        }
        & input[type="checkbox"]:checked + label::after {
            opacity: 1;
            }
        & input[type="checkbox"]:not(:checked) + label::after {
            opacity: 0;
            }
    }
}